.navItem {
    display: inline-block;
    padding-right: 1rem;
  }
  
  .navItem:last-child {
    padding-right: 0;
  }
  
  .navItem a {
    color: var(--text-color);
    font-size: 0.9rem;
    font-weight: bold;
    text-transform: uppercase;
    opacity: 0.6;
    text-decoration: none;
  }
  
  .navItem a:hover,
  .navItem a.active {
    color: var(--secondary-color);
    transition: 0.3s;
  }
  
  /* Mobile */
  @media screen and (max-width: 767px) {
    .navItem {
      text-align: center;
      width: 30%;
      padding: 1rem 0;
      border-bottom: 1px solid white;
    }
  
    .navItem:last-child {
      border-bottom: 0;
    }
  
    .navItem a {
      color: white;
      opacity: 1;
    }
  
    .navItem a.active {
      color: var(--primary-color);
      transition: 0.3s;
    }
  }